html,
body {
  box-sizing: border-box;
}
body {
  font-family: 'Helvetica Neue', sans-serif;
  margin: 0;
}
.todoListApp {
  max-width: 530px;
  padding: 0 15px;
  margin: 0 auto;
}
.todoListApp ul {
  list-style: none;
  padding: 0;
  margin: 0;
  border: 1px solid;
}
.todoListApp ul li {
  line-height: 35px;
  padding: 0 10px;
}
.todoListApp ul li.completed {
  text-decoration: line-through;
}
.todoListApp ul li:nth-child(even) {
  background: #eee;
}
.todoListApp ul li:nth-child(odd) {
  background: #fff;
}
.todoListApp .todoListForm {
  display: flex;
  height: 35px;
  border: 1px solid;
  border-top: none;
}
.todoListApp .todoListForm input[type="text"] {
  width: calc(100% - 150px);
  font-size: inherit;
  border: none;
  padding: 0 10px;
}
.todoListApp .todoListForm input[type="text"]::-webkit-input-placeholder {
  opacity: 0.65;
}
.todoListApp .todoListForm input[type="submit"] {
  width: 150px;
  font-weight: bold;
  border: none;
  border-left: 1px solid;
  background: #c5c5c5;
  cursor: pointer;
  transition: all 0.3s;
}
.todoListApp .todoListForm input[type="submit"]:hover {
  background: #b1b1b1;
}
